






























































import uniSegmentedControl from '../../../components/uni-tab-nav.vue';
export default {
	components: {
		uniSegmentedControl
	},
	data() {
		return {
			credit_id: '',
			card: [],
			list:[],
			loading: false,
			items: [
				'进行中',
				'已失败',
				'已取消',
				'已完成',
				'全部',
			],
			current: 0,
			activeColor: '#fe5950',
			styleType: 'text',
		}
	},
	computed: {

	},
	onLoad(e) {
		if (e.credit_id) {
			this.credit_id = e.credit_id;
		} else {
			uni.switchTab({
				url: '/pages/tabBar/card/card'
			})
		}
	},
	onShow() {
		this.getRepayInfo();
	},
	onBackPress() {
		uni.switchTab({
			url: '/pages/tabBar/card/card'
		})
	},
	methods: {
		goRepayDetail(pro_id){
			uni.navigateTo({
				url: '/pages/card/credit/repayDetail?credit_id='+this.credit_id+'&pro_id='+pro_id
			})
		},
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		},
		getRepayInfo() {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const credit_id = this.credit_id;
			try {
				this.$http.post(this.websiteUrl + '/api/repay/getRepayInfo', {
					openid,
					sessionKey,
					credit_id
				}, (res) => {
					if (res.data.code == 1) {
						this.card = res.data.result.creditCard;
						this.list = res.data.result.list;
					}else{
						uni.showModal({
							content:res.data.msg,
							showCancel:false
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false
				})
			}
		}
	}
}
